import React from 'react'
import "./contact.css";
import Phone from "../../../images/phone.png";
import Email from "../../../images/email.png";
import Address from "../../../images/address.png";
import Github from "../../../images/GitHub.png";
import Linkedin from "../../../images/Linkedin.png";
import emailjs from "emailjs-com";
import { useContext, useRef, useState } from "react";
import { ThemeContext } from "../../../context";

const Contact = () => {
    const formRef = useRef();
    const [done, setDone] = useState(false)
    const theme = useContext(ThemeContext);
    const darkMode = theme.state.darkMode;
    const handleSubmit = (e) => {
        e.preventDefault();
        emailjs
          .sendForm(
            "service_fyuo2mj",
            "template_5nbpcmq",
            formRef.current,
            "user_I2wQdZx9OjCuZKsnWi3MG"
          )
          .then(
            (result) => {
              console.log(result.text);
              setDone(true)
            },
            (error) => {
              console.log(error.text);
            }
          );
      };
    return (
        <div className="c">
        <div className="c-bg"></div>
        <div className="c-wrapper">
            <div className="c-left">
            <h1 className="c-title">Let's discuss your project</h1>
            <div className="c-info">
            <div className="c-info-item">
              <img src={Phone} alt="" className="c-icon" />
              +91 6289718944
            </div>
            <div className="c-info-item">
              <img className="c-icon" src={Email} alt="" />
              supratimsarkar2001@gmail.com
            </div>
            <div className="c-info-item">
              <img className="c-icon" src={Linkedin} alt="" />
              linkedin.com/in/supratim-sarkar-4528a91a8
            </div>
            <div className="c-info-item">
              <img className="c-icon" src={Github} alt="" />
              SupratimSarkar2001
            </div>
            <div className="c-info-item">
              <img className="c-icon" src={Address} alt="" />
              9/7/34 Bagmore,Kanchrapara,24 PGS(N),WB,India.
            </div>
            </div>
            </div>
            <div className="c-right">
            <p className="c-desc">
            <b>What’s your story?</b> Get in touch. Always available for
            freelancing if the right project comes along. me.
            </p>
            <form ref={formRef} onSubmit={handleSubmit}>
            <input type="text"  style={{backgroundColor: darkMode && "#333"}} placeholder="Name" name="user_name" />
            <input type="text" style={{backgroundColor: darkMode && "#333"}} placeholder="Subject" name="user_subject" />
            <input type="text" style={{backgroundColor: darkMode && "#333"}} placeholder="Email" name="user_email" />
            <textarea rows="5" style={{backgroundColor: darkMode && "#333"}} placeholder="Message" name="message" />
            <button>Submit</button>
            {done && <div className="greet">"Thank You!"</div>}
          </form>
            {/* footer is additional */}
            <div className="footer">
             <div className="footer-wrapper">
                 <div className="footer-left">
                     <div className="footer-left-component1"></div>
                     <div className="footer-left-component2"></div>
                 </div>
                 <div className="footer-right"></div>
             </div>
            </div>
            {/* end of footer */}
            </div>
        </div>
        </div>
    )
}

export default Contact
